import React, {Component} from 'react';
import axios from 'axios';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import './Trade.css';
import Typography from "@material-ui/core/es/Typography/Typography";


class Trade extends Component {

    state = {
        tradeList: [],
    };

    constructor() {
        super();
    }

    componentDidMount() {
        this.query();
    }

    query = () => {
        axios.get('/trade/findAll').then(response => {
            console.log('tradeList', response.data.data)
            this.setState({
                tradeList: response.data.data
            });
        });
    };


    render() {
        return (
            <Paper className="orderBook">
                <Typography className="orderText" variant="h2" align="center">Trade</Typography>
                <Table size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell align="right">Stock Name</TableCell>
                            <TableCell align="right">Buy Name</TableCell>
                            <TableCell align="right">Sell Name</TableCell>
                            <TableCell align="right">Price</TableCell>
                            <TableCell align="right">Quantity</TableCell>
                            <TableCell align="center">Trade Date</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.state.tradeList.map(trade => (
                            <TableRow key={trade.tradeId}>
                                <TableCell align="right">{trade.stockName}</TableCell>
                                <TableCell align="right">{trade.buyname}</TableCell>
                                <TableCell align="right">{trade.sellname}</TableCell>
                                <TableCell align="right">{trade.price}</TableCell>
                                <TableCell align="right">{trade.qty}</TableCell>
                                <TableCell align="center">{trade.tradeDate}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </Paper>
        );
    }

}

export default Trade;