import React, {Component} from 'react';
import axios from 'axios';
import Paper from '@material-ui/core/Paper';
import './orderBook.css';
import MaterialTable from "material-table";
import AddBox from "@material-ui/icons/AddBox";
import ArrowUpward from "@material-ui/icons/ArrowUpward";
import {forwardRef} from 'react';

import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import {injectMessageManager} from "react-message-manager";

const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref}/>),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref}/>),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref}/>),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref}/>),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref}/>),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref}/>),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref}/>),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref}/>),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref}/>),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref}/>),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref}/>),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref}/>),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref}/>),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref}/>),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref}/>),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref}/>),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref}/>)
};

class OrderList extends Component {

    state = {
        data: [],
        userMap: {},
        stockMap: {}
    };

    constructor() {
        super();
    }

    componentDidMount() {
        this.query();
        this.getColumn();
    }

    getColumn = () => {
        const columns = [
            {
                title: 'User Name',
                field: 'userId',
                type: 'string',
                lookup: this.state.userMap,
            },
            {
                title: 'Stock Name',
                field: 'stockId',
                type: 'string',
                lookup: this.state.stockMap,
            },
            {
                title: 'Type',
                field: 'type',
                type: 'string',
                lookup: {'B': 'Buy', 'S': 'Sell'},
            },
            {
                title: 'Order Type',
                field: 'orderType',
                type: 'string',
                lookup: {
                    'MKT': 'Market Order',
                    'LMT': 'Limit Order',
                    'STP': 'Stop Order',
                    'MIT': 'Market if Touched',
                    'LIT': 'Limit if Touched'
                },
            },
            {
                title: 'Price',
                field: 'price',
                type: 'numeric',
            },
            {
                title: 'Quantity',
                field: 'qty',
                type: 'numeric',
            },
            {
                title: 'Remain Qty',
                field: 'remainQty',
                type: 'numeric',
                editable: 'never',
            },
            {
                title: 'Market Price',
                field: 'marketPrice',
                type: 'numeric',
            },
            {
                title: 'Limit Price',
                field: 'limitPrice',
                type: 'numeric',
            },
            {
                title: 'FOK',
                field: 'fullOrKill',
                type: 'numeric',
                lookup: {0: 'Not FOK', 1: 'FOK'},
            },
            {
                title: 'Duration',
                field: 'duration',
                type: 'string',
                lookup: {'Day': 'DAY', 'GTC': 'GTC'},
            },
            {
                title: 'Submit Date',
                field: 'submitDate',
                type: 'datetime',
                editable: 'never',
            },
            {
                title: 'Status',
                field: 'status',
                type: 'numeric',
                editable: 'never',
                lookup: {
                    0: 'Not Submit',
                    1: 'Submitted',
                    2: 'Completed',
                    3: 'User Cancelled',
                    4: 'Over time',
                    5: 'System Cancelled'
                },
            },
        ];
        return columns
    };

    query = () => {
        axios.get('/Order/findAll').then(response => {
            this.setState({
                data: response.data.data
            });
        });
        axios.get('/Order/findAllUser').then(response => {
            this.setState({
                userMap: JSON.parse(response.data.data)
            });
        });
        axios.get('/stock/findAllStock').then(response => {
            this.setState({
                stockMap: JSON.parse(response.data.data)
            });
        })
    };

    render() {
        const self = this;
        return (
            <Paper className="listRoot">
                <MaterialTable
                    icons={tableIcons}
                    title="Order List"
                    id="materialTable"
                    style={{height: "100%"}}
                    data={this.state.data}
                    columns={this.getColumn()}
                    editable={{
                        onRowAdd: newData =>
                            new Promise(resolve => {
                                setTimeout(() => {
                                    resolve();
                                    const data = [...this.state.data];
                                    data.push(newData);
                                    this.setState({...this.state, data});
                                    axios({
                                        method: 'post',
                                        url: '/Order/add',
                                        data: newData
                                    })
                                        .then(function (response) {
                                            console.log('response: ', response.data.msg);
                                            if (response.data.msg !== "Add Success") {
                                                alert(response.data.msg)
                                            }
                                        })
                                        .catch(function (error) {
                                            console.log('error: ', error);
                                        });
                                }, 600);
                            }),
                        onRowDelete: oldData =>
                            new Promise(resolve => {
                                setTimeout(() => {
                                    resolve();
                                    const data = [...this.state.data];
                                    data.splice(data.indexOf(oldData), 1);
                                    this.setState({...this.state, data});
                                }, 600);
                            }),
                    }}
                    options={{
                        filtering: true,
                        sorting: true,
                        // padding: 'dense',
                        // headerStyle: {padding: '10px', fontSize: '0.8rem', fontWeight: 'bold'},
                        headerStyle: {fontSize: '0.8rem', fontWeight: 'bold'},
                    }}
                />
            </Paper>
        );
    }

}

export default injectMessageManager(OrderList);