import React, {Component} from 'react';
import './login.css';
import axios from 'axios';
import Typography from "@material-ui/core/es/Typography/Typography";
import TextField from "@material-ui/core/es/TextField/TextField";
import FormGroup from "@material-ui/core/es/FormGroup/FormGroup";
import FormControlLabel from "@material-ui/core/es/FormControlLabel/FormControlLabel";
import Checkbox from "@material-ui/core/es/Checkbox/Checkbox";
import Button from "@material-ui/core/es/Button/Button";
import {injectMessageManager} from "react-message-manager";

class Login extends Component {

    state = {
        username: '',
        password: '',
        rememberMe: true,
    };

    constructor(){
        super();
    }

    showLoginSuccess = () => {
        const { messageManager } = this.props;
        messageManager.showSuccessMessage('Log in Success.', {
            displayTime: 5000, //defaults to 2000
        });
    };


    handleChange(fieldName: string, event: any) {
        this.setState({
            [fieldName]: event.target.value,
            [`${fieldName}Error`]: false,
        });
    }

    handleRememberMeCheck(event: any) {
        this.setState({
            rememberMe: event.target.checked
        });
    }

    handleLogin() {
        if (!this.state.username || !this.state.password) {
            return;
        }
        const self = this;
        let history = this.props.history;
        let data = new URLSearchParams();
        data.append('username', this.state.username);
        data.append('password', this.state.password);
        axios({
            method: 'post',
            url: '/user/login',
            data: data
        })
            .then(function (response) {
                console.log('response: ', response.data.data);
                if (response.data.status === 'SUCCESS') {
                    self.showLoginSuccess();
                    const userName = response.data.data.username;
                    const userId = response.data.data.userId;
                    history.push({pathname:'/',  state: { userName: userName, userId:userId }});
                } else {
                    alert(response.data.msg);
                }
            })
            .catch(function (error) {
                console.log('error: ', error);
            });

    }

    handleRegister() {
        let history = this.props.history
        history.push('/register');
    }

    render() {
        return (
            <div className="login">
                <div className="content">
                    <Typography className="loginText" color="primary" variant="h2" align="center">Login</Typography>
                    <TextField
                        value={this.state.username}
                        onChange={this.handleChange.bind(this, 'username')}
                        label="Please input your username"
                        margin="normal"
                        autoFocus
                        fullWidth
                        required
                    />
                    <TextField
                        value={this.state.password}
                        onChange={this.handleChange.bind(this, 'password')}
                        label="Please input your password"
                        type="password"
                        margin="normal"
                        fullWidth
                        required
                    />
                    <div className="minHeight"/>
                    <div className="hide">
                        <FormGroup>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={this.state.rememberMe}
                                        onChange={this.handleRememberMeCheck.bind(this)}
                                        color="primary"
                                    />
                                }
                                label="Remeber me"
                            />
                        </FormGroup>
                    </div>
                    <div className="footer">
                        <Button
                            color="primary"
                            variant="contained"
                            style={{ color: "white" }}
                            onClick={this.handleLogin.bind(this)}
                            className="logFoot"
                        >
                            Log in
                        </Button>
                        <Button
                            color="secondary"
                            variant="contained"
                            style={{ marginLeft: '50px',color: "white" }}
                            onClick={this.handleRegister.bind(this)}
                            className="logFoot"
                        >
                            Register
                        </Button>
                    </div>
                </div>
            </div>
        );
    }

}

export default injectMessageManager(Login);