import React, {Component} from 'react';
import '../App.css';
import axios from 'axios';
import Typography from "@material-ui/core/es/Typography/Typography";
import TextField from "@material-ui/core/es/TextField/TextField";
import Button from "@material-ui/core/es/Button/Button";

class Register extends Component {

    state = {
        username: '',
        password: '',
        name: '',
    };

    handleChange(fieldName: string, event: any) {
        this.setState({
            [fieldName]: event.target.value,
            [`${fieldName}Error`]: false,
        });
    }

    handleRegister = (item) => {
        if (!this.state.username || !this.state.password) {
            return;
        }
        let history = this.props.history;
        let data = new URLSearchParams();
        data.append('username', this.state.username);
        data.append('password', this.state.password);
        data.append('name', this.state.name);
        axios({
            method: 'post',
            url: '/user/register',
            data: data
        })
            .then(function (response) {
                console.log('response: ', response.data);
                const { messageManager } = this.props;
                if (response.data.status === 'SUCCESS') {
                    messageManager.showSuccessMessage(response.data.msg + 'Please login.', {
                        displayTime: 5000, //defaults to 2000
                    });
                    history.push('/login');
                } else {
                    messageManager.showErrorMessage(response.data.msg, {
                        displayTime: 5000, //defaults to 2000
                    });
                }
            })
            .catch(function (error) {
                console.log('error: ', error);
            });
    };

    handleReset = (item) => {
        this.setState({
            username: '',
            password: '',
            name: ''
        })
    };

    render() {
        return (
            <div className="login">
                <div className="content">
                    <Typography className="loginText" color="primary" variant="h2" align="center">Register</Typography>
                    <TextField
                        value={this.state.username}
                        onChange={this.handleChange.bind(this, 'username')}
                        label="Please input your username"
                        margin="normal"
                        autoFocus
                        fullWidth
                        required
                    />
                    <TextField
                        value={this.state.password}
                        onChange={this.handleChange.bind(this, 'password')}
                        label="Please input your password"
                        type="password"
                        margin="normal"
                        fullWidth
                        required
                    />
                    <TextField
                        value={this.state.name}
                        onChange={this.handleChange.bind(this, 'name')}
                        label="Please input your name"
                        type="name"
                        margin="normal"
                        fullWidth
                        required
                    />
                    <div className="minHeight"/>
                    <div className="footer">
                        <Button
                            color="primary"
                            variant="contained"
                            style={{ color: "white" }}
                            onClick={this.handleRegister.bind(this)}
                            className="logFoot"
                        >
                            Register
                        </Button>
                        <Button
                            color="secondary"
                            variant="contained"
                            style={{ marginLeft: '50px',color: "white" }}
                            onClick={this.handleReset.bind(this)}
                            className="logFoot"
                        >
                            Reset
                        </Button>
                    </div>
                </div>
            </div>
        );
    }

}

export default Register;