import React, {Component} from 'react';
import './home.css';
import axios from 'axios';
import OrderBook from "../orderBook/orderBook";
import Tabs from "@material-ui/core/es/Tabs/Tabs";
import Tab from "@material-ui/core/es/Tab/Tab";
import OrderList from "../orderBook/orderList";
import logo from './logo.png';
import Button from "@material-ui/core/es/Button/Button";
import Trade from "../Trade/Trade";
import cookie from "react-cookie";
import Login from '../login/login';
import Link from "@material-ui/core/es/Link/Link";

class Home extends Component {

    state = {
        tabValue: 'order',
        open: false,
        userName: 'gtc',
        userId: 0,
    };

    componentDidMount() {
        if (this.props.location.state.userId !== 0){
            this.setState({
                userId: this.props.location.state.userId
            })
        }
    }

    onClose = (action: string) => {
        console.log(action);
        this.setState({
            open: false,
        });
    };

    onOpen = () => {
        this.setState({
            open: true,
        });
    };

    handleChange = (event, tabValue) => {
        this.setState({tabValue});
    };

    onLogin(userId) {
        // this.setState({ userId });
        // cookie.save('userId', userId, { path: '/' });
    }

    onLogout() {
        // cookie.remove('userId', { path: '/' });
        this.setState({
            userId: 0
        })
    }

    render() {
        if (this.state.userId === 0) {
            return <Login />;
        }
        return (
            <div className="all">
                <div className="user">
                    <img src={logo} className="img"/>
                    <div className="link">
                        <div className="welcome">
                            welcome, {this.props.location.state.userName}
                        </div>
                        <Button
                            onClick={this.onLogout.bind(this)}
                            style={{padding: "6px 16px"}}
                        >
                            logout
                        </Button>
                    </div>
                </div>
                <Tabs
                    value={this.state.tabValue || 'orderBook'}
                    onChange={this.handleChange}
                    // textColor="secondary"
                    // orientation='vertical'
                    // indicatorColor="primary"
                    // textColor="primary"
                    centered
                >
                    <Tab label="Order" value="order"/>
                    <Tab label="Trade" value="trade"/>
                </Tabs>
                {
                    this.state.tabValue === 'order' &&
                    <div className="orders">
                        <OrderBook/>
                        <OrderList/>
                    </div>
                }
                {this.state.tabValue === 'trade' && <Trade/>}
                {/*<div>*/}
                {/*<Button onClick={() => this.onOpen()} variant="contained"></Button>*/}
                {/*<Dialog*/}
                {/*open={this.state.open}*/}
                {/*onClose={(action) => this.onClose(action)}*/}
                {/*buttons={{*/}
                {/*ok: {*/}
                {/*color: 'secondary',*/}
                {/*text: '',*/}
                {/*className: 'ok',*/}
                {/*disabled: false,*/}
                {/*onClick: this.handleClick*/}
                {/*},*/}
                {/*cancel: {*/}
                {/*text: '',*/}
                {/*className: 'tt',*/}
                {/*}*/}
                {/*}}*/}
                {/*>*/}
                {/*<div>*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*666666666666666666666666666666666666666666666666*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*<br />*/}
                {/*</div>*/}
                {/*</Dialog>*/}
                {/*</div>*/}
            </div>
        );
    }

}

export default Home;