import React, {Component} from 'react';
import axios from 'axios';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import s from './orderBook.css';
import Typography from "@material-ui/core/es/Typography/Typography";
import Websocket from 'react-websocket';
import Button from "@material-ui/core/es/Button/Button";


class OrderBook extends Component {

    state = {
        buyOrderBook: [],
        sellOrderBook: [],
        stockList: [],
        willMatchBobId: 0,
        willMatchSobId: 0,
        selectStock: 0,
    };

    constructor() {
        super();
    }

    componentDidMount() {
        this.query();
    }

    query = () => {
        axios.get('/buyOrderBook/findAll').then(response=>{
            console.log("response.data", response.data)
            this.setState({
                buyOrderBook: response.data.data.sort(this.compare("DESC", 'buyPrice'))
            });
        });
        axios.get('/sellOrderBook/findAll').then(response=>{
            this.setState({
                sellOrderBook: response.data.data.sort(this.compare("ASCE", 'askPrice'))
            });
        });
        axios.get('/stock/stockPrice').then(response=>{
            console.log("response.data", response.data)
            this.setState({
                stockList: response.data.data,
                selectStock: response.data.data[0].stock_Id
            });
        });
    };

    compare(sortBy, event){
        return function(a,b){
            var value1 = a[event];
            var value2 = b[event];
            if (sortBy === "DESC") {
                return value2 - value1;
            } else if (sortBy === "ASCE") {
                return value1 - value2;
            }
        }
    }

    handleData(data) {
        let result = JSON.parse(data);
        console.log('result', result);
        if(result.msg === "Buy") {
            this.setState({
                buyOrderBook: result.data.sort(this.compare("DESC", 'buyPrice')),
                willMatchBobId: -1
            });
        }
        if(result.msg === "Sell") {
            this.setState({
                sellOrderBook: result.data.sort(this.compare("ASCE", 'askPrice')),
                willMatchSobId: -1
            });
        }
        if(result.msg === "left") {
            this.setState({
                willMatchBobId: result.data.bobId
            });
        }
        if(result.msg === "right") {
            this.setState({
                willMatchSobId: result.data.sobId
            });
        }
    }

    selectStock(id: number) {
        this.setState({
            selectStock: id,
        })
    }

    render() {
        return (
            <div className="orderBookAll" style={{ marginRight: "20px"}}>
                <Websocket url='ws://localhost:8080/websocket'
                           onMessage={this.handleData.bind(this)}/>
                <Paper className="stockList">
                    <Typography className="orderText" variant="h6" align="center">Stock List</Typography>
                    <Table size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell align="right">Stock</TableCell>
                                <TableCell align="right">Code</TableCell>
                                <TableCell align="right">Buy</TableCell>
                                <TableCell align="right">Sell</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.state.stockList.map(stock => (
                                <TableRow
                                    key={stock.stock_Id}
                                    onClick={() => this.selectStock(stock.stock_Id)}
                                    selected={stock.stock_Id === this.state.selectStock}
                                >
                                    <TableCell align="right">{stock.stockName}</TableCell>
                                    <TableCell align="right">{stock.stockCode}</TableCell>
                                    <TableCell align="right">{stock.buy_price}</TableCell>
                                    <TableCell align="right">{stock.sell_price}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Paper>
                <Paper className="orderBookList">
                    <Typography className="orderText" variant="h6" align="center">Order Book</Typography>
                    <div className="table">
                        <Table size="small">
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center" colSpan={2}>Bid</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell align="right">Qty</TableCell>
                                    <TableCell align="right">Price</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {this.state.buyOrderBook.filter(o => o.stockId === this.state.selectStock).map(order => (
                                    <TableRow key={order.bobId} className={order.bobId === this.state.willMatchBobId ? "willMatch" : null}>
                                        <TableCell align="right">{order.buySize}</TableCell>
                                        <TableCell align="right">{order.buyPrice}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                        <Table size="small">
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center" colSpan={2}>Ask</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell align="right">Price</TableCell>
                                    <TableCell align="right">Qty</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {this.state.sellOrderBook.filter(o => o.stockId === this.state.selectStock).map(order => (
                                    <TableRow key={order.sobId}  className={order.sobId === this.state.willMatchSobId ? "willMatch" : null}>
                                        <TableCell align="right">{order.askPrice}</TableCell>
                                        <TableCell align="right">{order.askSize}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </Paper>
            </div>
        );
    }

}

export default OrderBook;